form{
    background-color:azure ;
    border: solid black 1px;
    border-radius: 20px;
    margin: 10px;
    width: min-content;
    box-shadow: 0px 0px 1px 0px;
}

h4{
    display: flex;
    justify-content: center;
    font-size: xx-large;
}
input{
    border-radius: 6px;
    border: solid green 1px;
    margin: 10px;
    cursor: pointer;
}
button{
    border-radius: 6px;
    padding: 8px;
    border:solid black 1px;
    background-color: slateblue;
    color: aliceblue;
    cursor: pointer;
}
